
import { request, address } from '../utils/HttpRequest/request'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { ossPrefix } from './../config'
import { wishes } from './../config/variables'
import { mapActions, mapState } from 'vuex'
import greetingApis from './../store/apis/greeting'
export default {
components: {
Flexbox,
FlexboxItem,
XHeader,
XButton,
Toast
},
data () {
return {
ossPrefix,
total:0,
timer: null,
wishList: wishes,
wishText:'',
index:1,
dis:false,
defaultAvatar: `${ossPrefix}tx.png`,
toastType:'',//提示类型
toastText:'',//提示文字
showToast:false,//提示信息
}
},
methods: {
...mapActions({
getCurrentGreetings: 'getCurrentGreetings'
}),

changeText () {
this.index = Math.floor(Math.random()*this.wishList.length);
this.wishText = this.wishList[this.index].text;
this.getWish(1)
},

sendWish () {
console.log('INIT:', this.currentGreetings)
const content = this.wishText
greetingApis.create(content).then((res) => {
if (res.code !== 200) {
this.$vux.toast.text(res.message)
} else {
this.$vux.toast.text('感谢您的祝福, 请明天再来')
this.getWish(1)
}
})
},

getWish (page) {
this.getCurrentGreetings(page)
}
},
mounted () {
this.changeText();
this.timer = setInterval(() => {
this.getWish();
}, 30000)
},
computed: {
...mapState({
currentGreetings: state => state.greeting.list,
greetingAmount: state => state.greeting.total
})
},
beforeDestroy () {
console.log(this.timer, 'timer')
clearInterval(this.timer)
}
}
